.MuiToolbar-regular {
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.header__optionLineOne {
  font-size: 13px;
}

.header__optionBasket {
  color: black;
}

.header__nav {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
}

.header__option {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-left: 10px;
  margin-right: 10px;
  color: black;
}

.header__nav > a {
  text-decoration: none;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.header__optionLineOne:hover {
  color: lightgray;
  transition: all 0.2s ease-in-out;
}

.MuiSvgIcon-root {
  color: black;
}

.MuiSvgIcon-root:hover {
  color: lightgray;

}

.home {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-left: auto;
  margin-right: auto;
  max-width: 1500px;
}

.home__product {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 40px;
  justify-items: center;
}

.home__productscontainer {
  max-width: 1560px;
  min-width: 260px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
  padding-left: 30px;
  padding-right: 30px;
}

.home__sectionheading {
  margin-bottom: 20px;
  /* padding-left: 10px; */
}

.home__sectionheading > h3 {
  font-size: 25px;
  margin-bottom: 3px;
}

.home__sectionheading > p {
  font-size: 13px;
}

/* .home__row{
display: flex;

margin-left: 5px;
margin-right: 5px;

} */

.home__image {
  width: 100%;
}

@media only screen and (min-width: 500px) {
  .home__product {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

@media only screen and (min-width: 900px) {
  .home__product {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}

.product {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;

  -webkit-align-items: center;

          align-items: center;

  /* margin: 10px;
  padding: 20px;

  max-height: 400px; */
  min-width: 100px;
  max-width: 300px;
}

.product > img {
  max-height: 300px;
  width: 100%;
  object-fit: contain;
  margin-bottom: 15px;
}

.product__info > button {
  background: #d8d8da;
  border: none;
  color: black;
  border-color: none;
  outline: none;
  font-size: 12px;
  border-radius: 10px;
  padding: 7px 20px 7px;
  cursor: pointer;
}

.product__info > button:hover{

  color: #d8d8da;
  background: black;
  transition: all 0.2s ease-in-out;
}



.product__price {
  margin-top: 5px;
  margin-bottom: 15px;
}

.product__info {
  margin-bottom: 3px;
  text-align: left;
  width: 100%;
  /* padding-left: 10px; */
}

.checkout {
  display: -webkit-flex;
  display: flex;

  background-color: white;
  height: -webkit-max-content;
  height: max-content;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.checkout__ad {
  width: 100%;
  margin-bottom: 10px;
}

.checkout__title {
  margin-right: 10px;
  padding: 10px;
  
  font-size: 25px;
}

.checkout__left{

padding:30px ;
display: -webkit-flex;
display: flex;
-webkit-flex-direction: column;
        flex-direction: column;
}

.checkout__right {
  -webkit-align-self: center;
          align-self: center;

 
}


@media only screen and (min-width: 900px) {
  .checkout__left {
    -webkit-flex-direction: row;
            flex-direction: row;
  }
}
.checkoutProduct {
  display: -webkit-flex;
  display: flex;
 padding: 10px 0px;
 border-bottom: 1px solid lightgray;
}

.checkoutProduct__info {
  padding-left: 20px;
}

.checkoutProduct__info > button {
  background: #d8d8da;
  border: none;
  margin-top: 10px;
  font-size: 12px;
  border-radius: 10px;
  padding: 10px 20px ;
  cursor: pointer;
  color: black;
  outline: none;
}

.checkoutProduct__info > button:hover {

    color: #d8d8da;
    background: black;
    transition: all 0.2s ease-in-out;
}


.checkoutProduct__image {
  width: 180px;
  height: auto;
}

.checkoutProduct__rating {
  display: -webkit-flex;
  display: flex;
}

.checkoutProduct__title {
  font-size: 17px;
  font-weight: 800;
  width: 350px;
  margin-bottom: 5px;
}

